<template>
  <div class="container">
    <h1>News Feed</h1>
    
      <div class="row" v-for="item in feedItems" :key="item.title">
        <div class="container">
          <h4>{{item.title}}</h4>
          <img v-if="item.image !== null" v-bind:src="item.image" alt="" height="128" width="128">
        </div>
      </div>
  </div>
</template>

<script>

import rssParser from "rss-parser"

const CORS_PROXY = "https://cors-anywhere.herokuapp.com/"

export default {
  name: 'NewsFeed',
   data() {
    return {
      feedItems: []
    };
  },
  async mounted() {
    let regex = new RegExp('<img[^>]+src="([^">]+)');
    let parser = new rssParser();
    let feed = await parser.parseURL(CORS_PROXY + 'https://techcrunch.com/feed/');
    console.log(feed.title);
    this.feedItems = feed.items.map(item => {
      let imgArr = item["content:encoded"].match(regex);
      return {
        title: item.title,
        link: item.link,
        image: !imgArr || imgArr.length === 0 ? null : imgArr[1],
        feed: feed.title
      }

    });
    console.log(this.feedItems);
  }

}
</script>

